@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;1,300&family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@200;300;400&family=Quicksand:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Launch-container
{
    padding: var(--xxl) var(--l);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.Launch-showContainer
{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 0;
}

.Launch-bodyContainer
{
    -webkit-flex-grow: 1;
            flex-grow: 1;
    -webkit-flex-basis: 0;
            flex-basis: 0;
}


.Launch-im
{
    border-radius: 25%;
    margin: 0;
}

.Launch-headContainer
{
    -webkit-flex-grow: 1;
            flex-grow: 1;
    -webkit-flex-basis: 0;
            flex-basis: 0;
    padding-left: var(--l);
}

.Launch-buttonContainer
{
    -webkit-flex-basis: 0;
            flex-basis: 0;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    margin: var(--m) 0;
}

.Launch-header 
{
    font-size: 100px;
    font-weight: 400;
    color: var(--primary);
    padding-bottom: var(--l);
    margin: none;

    border-bottom: 1px solid var(--primary--dim);
}

.Launch-subheader
{
    font-size: large;
    font-weight: 300;
    color: var(--primary);
    margin: 0;
    padding: var(--xl) var(--l);
}

.Launch-button {
    font-family: "Open Sans";
    font-size: 18px;
    padding: var(--s) var(--m);
    margin: 0 var(--m);
    color: var(--primary);
    border: var(--primary) 1px solid;
    border-radius: var(--xs);
    background: none;
    transition: color 0.5s, background 0.5s;
}
  
.Launch-button:hover 
{
    color: var(--white);
    background-color: var(--primary);
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
}

.Launch-bold:hover
{
    font-size: 24px;
}
  
.Sketch-container
{
    padding: var(--xl) var(--l);
}

.Sketch-header 
{
    font-size: 40px;
    color: var(--primary);
    font-weight: 400;
}
  
.Sketch-line 
{
    border-color: var(--grey);
}
  
.Sketch-subContainer 
{
    -webkit-flex-grow: 1;
            flex-grow: 1;
    -webkit-flex-basis: 0;
            flex-basis: 0;
}

.Sketch-subContainer
{
    margin-left: var(--m);
}

.Sketch-subTitle 
{
    font-weight: 400;
    font-size: 24px;
    color: var(--primary);
    margin: var(--m) var(--s);
}

.Sketch-image
{
    border-radius: var(--m);
    width: 350px;
    height: auto;
}
.NavBar-container {
    padding: var(--s) var(--m);
    background-color: var(--primary);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    z-index: 1;
  }
  
  .NavBar-title {
    color: var(--white);
    font-size: 20px;
  }
  
  .NavBar-linkContainer 
  {
    -webkit-flex-basis: 0;
            flex-basis: 0;
    margin-left: var(--s);
  }
  
  .NavBar-link,
  .NavBar-link:visited {
    color: var(--white);
    opacity: 0.6;
    text-decoration: none;
    padding: var(--m);
  }
  
  .NavBar-link:hover {
    opacity: 0.8;
  }
  
  .NavBar-link + .NavBar-link {
    margin-left: var(--m);
  }
  
